import '../../../static/css/person/iconfont.css'
import LoadingImg from '../../public/loadingImg'
  export default {
      name: "orderFor",
    data(){
        return{
          api: this.GLOBAL.baseURL,
          order_id:'',
          orderInformation:'',
          type:''
        }
    },
    components:{
      LoadingImg
    },
    methods:{
      dispute(){
        this.$router.push('/dispute')
      },
      goBack(){
        this.$router.go(-1)
      },
      getRouter(){
        this.order_id = this.$route.query.order_id
        this.type = this.$route.query.type
        this.getOrderMation()
      },
      getOrderMation(){
        this.$http.post(this.api + '/OrderD',{
          token: localStorage.getItem('token'),
          order_id: this.order_id
        })
          .then(res=>{
            if(res.data.code == 1){
              this.orderInformation = res.data.data
            }else if(res.data.code == 3){
              this.getOrderMation()
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      refund(){
        this.$router.push({
          path:'/refund',
          query:{
            information: this.order_id,
            returnFace:this.orderInformation.refund_total_price
          }
        })
      }
    },
    mounted() {
        this.getRouter()
    }
  }



